import React from "react"
import { Link } from "gatsby"
import Bio from "../components/bio"
import { rhythm, scale } from "../utils/typography"
import { FaCalculator } from "react-icons/fa"

const Layout = ({ location, title, children }) => {
  const rootPath = `${__PATH_PREFIX__}/`
  let header

  if (location.pathname === rootPath) {
    header = (
      <h1
        style={{
          ...scale(1.5),
          marginBottom: rhythm(1.5),
          marginTop: 0,
        }}
      >
        <Link
          style={{
            boxShadow: `none`,
            color: `inherit`,
          }}
          to={`/`}
        >
          {title}
        </Link>
      </h1>
    )
  } else {
    header = (
      <h3
        style={{
          fontFamily: `Montserrat, sans-serif`,
          marginTop: 0,
        }}
      >
        <Link
          style={{
            boxShadow: `none`,
            color: `inherit`,
          }}
          to={`/`}
        >
          {title}
        </Link>
      </h3>
    )
  }

  const SectionPage = () => {
    return (
      <ul>
        <li><Link to="/">Article</Link></li>
        <li><Link to="/">Self-learning notes</Link></li>
      </ul>
    )
  }

  return (
    <div style={{boxFlex: '1', flexGrow: '1', marginLeft: 'auto', marginRight: 'auto'}}>
      <div style={{display: 'flex', marginLeft: 'auto', marginRight: 'auto', justifyContent: 'center'}}>
      <div
        className = "layout-aside-bio"
        style={{
          float: `left`,
          padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
          maxWidth: rhythm(12.85),
          position: `sticky`,
          height: `calc(100% - 2em)`,
          top: 0,
        }}>
            <Bio />
      </div>
      <div
        style={{
          maxWidth: rhythm(24),
          padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
          float: `right`,
        }}
      >
        <header>{header}</header>
        <main>{children}</main>
        <footer>
           pehcy {new Date().getFullYear()}, Built with
          {` `}
          <Link
            to = "https://www.gatsbyjs.org"
            style={{
              textDecoration: `none`,
              color: `inherit`,
            }}
          >
            Gatsby
          </Link>
        </footer>
      </div>
      </div>
    </div>
  )
}

export default Layout
